import './Card.css';
import Anchieta from '../Images/Anchieta.png'
import UNIP from '../Images/UNIP.png'
function Card(props) {
    return (
        <div id="Card">
                <img className="imageCard" src={props.image}/>   
                <div id="contentCard">
                    <p id="titleCard">{props.title}</p>
                    <p className="bodyCard">Instituição: {props.university}</p>
                    <p className="bodyCard">{props.time}</p>
                    <p className="bodyCard">STATUS: {props.status}</p>
                </div>
        </div>
    )
}
export default Card;